import { motion } from "framer-motion"
function CalButton({item,handleButtonClick}){
    let flag = false;
    if(isNaN(item) || item==='.') {flag=true;}
    return (<>
    
    
        <motion.button
         animate={{
            scale: [1, 2, 2, 1, 1],
            rotate: [0, 0, 270, 270, 0],
            borderRadius: ["20%", "20%", "50%", "20%", "50%"],
           
          }}
           className={`btn ${flag && 'org'}`  } onClick={()=>handleButtonClick(item)}>{item}</motion.button>
    </>);

}

export default CalButton;
