import CalButton from "./CalButton";
function ButtonArea({handleButtonClick}){

let buttonList = ['/','*','+','-','1','2' ,'3','%', '4','5','6','(','7','8','9',')','.','0','=','c'];
    return(<>
    { buttonList.map( item =>{

        return  <CalButton key={item} item={item} handleButtonClick={handleButtonClick} ></CalButton>
    }
)
}
        
    </>);
}

export default ButtonArea;

{/* <button className="btn " key={item} onClick={()=>handleButtonClick(item)}>{item}</button> */}