import { evaluate } from 'mathjs';
import { motion } from "framer-motion"
import './App.css';
import InputArea from './InputArea';
import ButtonArea from './ButtonArea';
import {useState} from "react";
import Background from './Background';

function App() {
let [displayVal , setDisplayVal]  =useState(" ");
const handleButtonClick = (item)=>{


if(item ==='c'){
  setDisplayVal("");
}
else if(item ==='='){


  try{
    let ans = evaluate(displayVal);
    setDisplayVal(ans);
  
  }
  catch(err){
    setDisplayVal("err");
  }
}
else{
  setDisplayVal(displayVal + item);
}

}

  return (
    <>
    <Background></Background>
    <motion.div
     initial={{ scale: 0 }}
     animate={{ rotate: 360, scale: 1 }}
     transition={{
       type: "spring",
       stiffness: 260,
       damping: 20,
      
     }}
  
      className="App">
    <motion.h1 
     initial={{ opacity: 0, scale: 0.5 }}
     animate={{ opacity: 1, scale: 1 }}
     transition={{ duration: 2 }}
   
     > CALCULATOR </motion.h1>
      <InputArea displayVal={displayVal} ></InputArea>
      <ButtonArea  handleButtonClick={handleButtonClick}></ButtonArea>
    </motion.div>
    
    </>
  );
}

export default App;
